.editor-actions{display:flex;align-items:center;justify-content:space-between;padding:0 30px;background-color:#eef4f5}.editor-actions>.button{position:relative}.editor-actions--error{position:absolute;left:calc(100% + 20px);width:210px;padding:.75em;display:inline-flex;align-items:center;justify-content:center;pointer-events:none;background-color:#3b4859;word-break:keep-all;border-radius:3px;font-size:11px}.editor-actions--error:before{content:"";position:absolute;left:-7px;width:0;height:0;border-top:7px solid transparent;border-bottom:7px solid transparent;border-right:7px solid #3b4859}