.format-button{display:inline-flex;align-items:center;padding:.75em 1em;cursor:pointer;outline:none;font-size:14px;color:#69a1ac;border:1px solid #69a1ac;border-radius:5px;transition:.2s}.format-button>svg{margin-right:.75em}.format-button:hover{color:#3b4859;border-color:#3b4859}.format-button:focus{box-shadow:0 0 3px 3px rgba(105,161,172,.5)}.format-button:disabled{background-color:rgba(59,72,89,.3);cursor:auto}