.header-button{display:inline-flex;align-items:center;justify-content:center;width:50px;height:30px;cursor:pointer;outline:none;border:1px solid #d2e3e7;border-radius:3px;background-color:#eef4f5;transition:.2s;padding:0}.header-button.active,.header-button:hover{border-color:#a6c5cc;background-color:#e4ebee}.header-button.active{color:#69a1ac}.header-button.active:focus{box-shadow:0 0 3px 3px #a6c5cc}.header-button:focus{box-shadow:0 0 3px 3px rgba(210,227,231,.5)}