.history{position:relative;width:100%;height:100%;display:flex;justify-content:flex-end;background-color:#eef4f5;border-top:1px solid #d2d5d9;font-family:Jost,sans-serif;font-weight:400}.history--clear{position:relative;outline:none;cursor:pointer;display:inline-flex;align-items:center;justify-content:center;width:36px;border:none;border-left:1px solid #d2d5d9;transition:.3s}.history--clear:focus{box-shadow:1px 1px 3px #b9d8dd}.history--clear:hover{background-color:#b9d8dd}.history--clear:before{content:"";position:absolute;z-index:1;pointer-events:none;width:20px;height:100%;left:-21px;background:linear-gradient(90deg,transparent,#f3f4f5)}.history--tabs{position:absolute;top:0;left:0;right:36px;height:calc(100% + 116px);display:flex;align-items:flex-start;-ms-overflow-style:none;scrollbar-width:none;overflow-x:auto}.history--tabs::-webkit-scrollbar{display:none}