.split-pane{position:relative;display:grid;grid-template-columns:1fr 1fr;gap:15px;width:100%;height:100%;padding:0;border-bottom:1px solid #d2d5d9;overflow:hidden}.split-pane--section{position:relative;max-width:100%}.split-pane--separator{position:absolute;display:flex;align-items:center;justify-content:center;flex-flow:column wrap;width:15px;height:100%;left:-15px;z-index:100;cursor:col-resize;-moz-user-select:none;-ms-user-select:none;-webkit-user-select:none;user-select:none}.split-pane--separator:before{content:"";position:absolute;display:inline-block;width:3px;height:70px;background-color:#d2d5d9;transition:all .3s}.split-pane--separator:hover:before{background-color:#c4c7ca}