.tab{position:relative;max-width:220px;height:36px;display:flex;align-items:center;justify-content:space-between;background-color:#eef4f5;transition:.15s;padding:5px 2px 5px 8px;border-top:2px solid #eef4f5;font-size:14px;opacity:.7;cursor:pointer}.tab,.tab>svg{flex-shrink:0}.tab--name{margin:0 1em;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.tab--more{display:inline-flex;align-items:center;border:none;padding:0;background-color:transparent;outline:none;cursor:pointer;transition:.15s;opacity:0}.tab--options{position:absolute;top:32px;left:0;width:100%;margin:0;padding-left:0;list-style:none;background-color:#fff;border-bottom-left-radius:4px;border-bottom-right-radius:4px;box-shadow:1px 3px 3px 1px #d2d5d9}.tab--options>li{display:flex;align-items:center;justify-content:flex-start;padding:.75em .5em;cursor:pointer}.tab--options>li>svg{margin-right:.5em}.tab--options>li:hover{background-color:#eef4f5}.tab--options>hr{height:1px;border:none;color:#d2d5d9;background-color:#d2d5d9;margin:0}.tab-active,.tab-selected{background-color:#fff;border-top-color:#3b4859;opacity:1;z-index:9999}.tab-active .tab--more,.tab-selected .tab--more{opacity:1}.tab-selected:not(.tab-active),.tab:hover:not(.tab-active){border-color:rgba(59,72,89,.4)}.tab:hover:not(.tab-active){background-color:#fff;opacity:1}.tab:hover:not(.tab-active) .tab--more{opacity:1}