.button{height:50px;cursor:pointer;outline:none;display:inline-flex;align-items:center;justify-content:center;background-color:#3b4859;border:none;border-radius:20px;padding:.75em 1.5em;color:#fff;text-transform:uppercase;font-weight:600;font-size:14px;transition:.2s;flex-shrink:0}.button>span.spinner,.button>svg{margin-left:.75em}.button:hover{background-color:rgba(59,72,89,.7)}.button:focus{box-shadow:0 0 3px 3px rgba(105,161,172,.5)}.button:disabled{background-color:rgba(59,72,89,.3);cursor:auto}